@import url(https://fonts.googleapis.com/css?family=Telex);
* {
  margin: 0;
}

body {
  background-color: rgb(234, 237, 237);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header__logo {
  width: 100px;
  height: 50px;
  object-fit: contain;
  margin: 0 10px;
  margin-top: 1px;
}

.header__search {
  display: flex;
  flex: 1 1;
  align-items: center;
  border-radius: 24px;
}
.navbuttn h4{
  padding: 9px;
  color: rgba(14, 248, 217, 0.719);
}
.navbuttn{
  display: flex;
  padding: 40px;
}
.header__searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 20%;
}

.header__searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: #cd9042;
  margin-top: -9px;
  border-radius: 6px;
}

.header__optionLineOne {
  font-size: 11px;
  font-family: monospace;
  margin-bottom: 2px;
}

.header__optionLineTwo {
  font-size: 14px;
  font-weight: 800;

}
 

.header__optionBasket {
  display: flex;
  align-items: center;
  color: white;
}

.header__basketCount {
  margin-left: 10px;
  margin-right: 10px;
}

.header__nav {
  display: flex;
  justify-content: space-evenly;
}

.header__option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}

.home {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
}

.home__row {
  display: flex;
  z-index: 1;
   margin-left: 15px;
  margin-right: 15px;
  width: auto;
  margin: auto;
}

.home__image {
  width: 100%;
  /* z-index: ; */
  margin-top: 10px;
  /* mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)); */
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 30px;
  padding: 25px;
  width: 70%;
  max-height: 400px;
  min-width: 100px;
  background-color: white;
  z-index: 1;
}

.product__rating {
  display: flex;
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product > button {
  background: #f0c14b;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}

.product__price {
  margin-top: 5px;
}

.product__info {
  height: 100px;
  margin-bottom: 15px;
}

.carousel-item-main{
  width: 18vw;
  height: 22vw;
  padding: 0vw 1vw 0vw 1vw;
  background: white;
  margin: 1vw 0.3vw 1vw 0.3vw;

}
.carousel-item-main:nth-child(1){
  margin-left: 33vw;
}

/*.carousel-item-image{
  width: 15vw;
}*/
.carousel-item-main h1{
  font-size: 1.5vw;
  font-weight: lighter;
}
.carousel-item-main div{
  width: 100%;
  height: 0.35vw;
}
.carousel-item-main p{
  font-size: 1vw;
}

.carousel-flex{
  display: flex;
  justify-content: center;
}
.carousel-and-arrows{
  position: relative;
  display: flex;
  justify-content: center;
}
.carousel-items{
  position: relative;
  overflow-x: scroll;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 60vw;
  background: #EAEDED;
  /*height: 10vw;*/
}
.item{
  background: red;
  margin: 0vw 2vw;
  width: 10vw;
  height: 8vw;
  flex: 0 0 auto;
}
.arrow{
  z-index: 500;
  position: absolute;
  top: 26%;
  height: 10vw;
}

.left-arrow{
  left: 0vw;

}
.right-arrow{
  right: -0.2vw;
  transform: scaleX(-1);
}

.top-slide-show-main{
  overflow: hidden;
  position: relative;
  width: 100%;
  display: flex;
  /*justify-content: space-around;*/
  align-items: center;
  margin-top: 0px;
  margin-bottom: -50px;
}
.top-slide-show-main a{
  color: black;
}
.top-slide-show-main h1{
  font-size: 50px;
  margin-top: 66px;
}
.slideshow-slide{
  position: relative;
  width: 100%;
}
.slide-arrow{
  position: absolute;
  top: 2vw;
}
.slide-arrow-left{
  opacity: 0.2;
  left: 0vw;
  z-index: 500;
}
.slide-arrow-right{
  right: 0vw;
}

.footer-container{
    width: 100%;
    height: 90vh;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: white;
}
.back-to-top {
    width: 100%;
    /* height: 40px; */
    background-color: #37475a;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    
    

}
.back-to-top-btn {
    color: white;
    text-decoration: none;
    font-weight: 700;
    padding: 10px;
    line-height: 19px;
    font-size: 11px;
}
.links-top-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: auto;
    background-color: #232f3e;
    height: 40vh;
    width: 100%;
}
.footer-table {
    /* background-color: orchid; */
}
.footer-table td {
    vertical-align: top;
}
.links-top-title {
    color: white;
    font-size: 13px;
    font-weight: 800;
    text-align: left;
    padding: 4px 10px;
    white-space: nowrap;

}
.links-top {
    color: #ccc;
    font-size: 13px;
    text-decoration: none;
}
.footer-link-list {
    /* background-color: purple;
    border: 1px solid white; */
    padding: 0;
    margin: 0;
}
.footer-link{
    text-align: left;
    list-style: none;
    white-space: nowrap;
}
.footer-link a {
    text-decoration: none;
    color: #ddd;
    font-size: 10px;
    line-height: 120%;
    padding: 0 10px;
    font-weight: 600;
}
.footer-link a:hover {
    text-decoration: underline;
}
.footer-link-spacing {
    width: 40px;
    /* background-color: greenyellow; */
}
.bottom-footer-top {
    border: none;
    border-top: .5px solid #3a4553;
    background-color: #232f3e;
    /* height: 300px;
    width: 100%; */
}

.amz-logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
     
}
.amz-logo {
    padding: 0 60px 0 0;
}

.language-btn {
    border-radius: 3px;
    border: 1px solid #848688;
    font-size: 11px;
    padding: 6px;
    margin: 0 4px;

}

.country-btn {
    border-radius: 3px;
    border: 1px solid #848688;
    font-size: 11px;
    padding: 6px;
    margin: 0 4px;
}
.fa-globe {
    font-size: 12px;
}
.links-bottom-container {
    background-color: #131a22;
    width: 100%;
    /* height: 100%; */
    font-family: Georgia, 'Times New Roman', Times, serif;
    padding-top: 30px;
    box-sizing: border-box;
}
.bottom-table {
    /* max-width: 700px; */
    max-width: 70%;
    margin: auto;
    border-collapse: collapse;
}
.bottom-links {
    text-align: left;
    line-height: 60%;
    /* color: white; */
}
.bottom-link {
    text-decoration: none;
    color: #ddd;
    font-weight: 700;
    font-size: 10px;
    
    white-space: nowrap;
    padding: 0px;
}
.bottom-link:hover {
    text-decoration: underline;
}
.bottom-link span {
    padding:0;
    font-weight: normal;
    font-size: 9px;
    color: #999;
    /* line-height: 115%; */
}
.bottom-table-height-spacing{
    height: 4px;
}
.bottom-table-spacing {
    min-width: 20px;
}
.copy-right {
    margin: auto;
    width: 700px;
    padding: 30px 0 30px;
    font-size: 10px;
    color: #ddd;
    font-weight: 700;
}
.copy-right-span {
    color: #999;
}
.checkout {
  display: flex;
  padding: 20px;
  background-color: white;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

.checkout__ad {
  width: 100%;
  margin-bottom: 10px;
}

.checkout__title {
  margin-right: 10px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}

.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 300px;
  height: 100px;
  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
}

.subtotal__gift {
  display: flex;
  align-items: center;
}

.subtotal__gift > input {
  margin-right: 5px;
}

.subtotal > button {
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}

.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.checkoutProduct__info {
    padding-left: 20px
}

.checkoutProduct__info > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.checkoutProduct__image {
    object-fit: contain;
    width: 180px;
    height: 180px;
  }

.checkoutProduct__rating {
    display: flex;
  }

.checkoutProduct__title {
    font-size: 17px;
    font-weight: 800;
}
.login {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
  }

.login__container {
    width: 300px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
  }

.login__container > form > h5 {
    margin-bottom: 5px;
}

.login__container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
  }

.login__container > p {
    margin-top: 15px;
    font-size: 12px;
}

.login__signInButton {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
  }

  .login__registerButton {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
  }
  
  
.payment {
    background-color: white;
}

.payment__container > h1 {
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: rgb(234, 237, 237);
    border-bottom: 1px solid lightgray;
  }

.payment__container > h1 a {
    text-decoration: none;
}

.payment__section {
    display: flex;
    padding: 20px;
    margin: 0 20px;
    border-bottom: 1px solid lightgray;
}

.payment__title {
    flex: 0.2 1;
}

.payment__address, .payment__items, .payment__details {
    flex: 0.8 1;
}
  
.payment__details > form {
    max-width: 400px;
  }

  .payment__details > h3 {
    padding-bottom: 20px;
  }


  .payment__details > form > div > button {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    font-weight: bolder;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
  }
.orders {
    padding: 20px 80px;
}

.orders > h1 {
    margin: 30px 0;
}
.order {
    padding: 40px;
    margin: 20px 0;
    border: 1px solid lightgray;
    background-color: white;
    position: relative;
}

.order__id {
    position: absolute;
    top: 40px;
    right: 20px;
  }

.order__total {
    font-weight: 500;
    text-align: right;
  }
  
